@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Catamaran', 'Bahnschrift', sans-serif;
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  background-color: #eef;
  min-height: 99vh;
}

.wrapper {
  max-width: 20rem;
  min-width: 20rem;
  background-color: #bfbfff;
  border-radius: 0.5rem;
  padding: 0.3rem;
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.todo__list__container {
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.todo__list__container::-webkit-scrollbar {
    background-color: #dde;
    width: 0.4rem;
    border-radius: 0.3rem;
}

.todo__list__container::-webkit-scrollbar-thumb {
    background-color: #bbc;
    width: 0.4rem;
    border-radius: 0.3rem;
}


.header {
	background: linear-gradient(55deg, #1a06b9, #0f0080);
	color: #fff;
	padding: 1.5rem;
	border-radius: 0.4rem;
}

.header h1 {
	font-size: 1.6rem;
	margin-bottom: 0.5rem;
}

.header .counter {
	font-size: 1rem;
}
.input__container {
	position: relative;
}

.form-control {
	padding: 0.2rem;
	background-color: #fff;
	border-radius: 0.4rem;
	display: flex;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
}

.todo__input {
	border: none;
	outline: none;
	padding-left: 1rem;
	margin-right: 0.4rem;
	width: 100%;
	font-size: 1rem;
}

.add__btn {
	padding: 0.4rem 1rem;
	color: #fff;
	border: none;
	outline: none;
	background: linear-gradient(55deg, #1a06b9, #0f0080);
	font-family: inherit;
	font-size: 1rem;
	border-radius: 0.4rem;
	cursor: pointer;
}

.start__adding__btn {
	border: none;
	outline: none;
	cursor: pointer;
	font-size: 2rem;
	width: 3rem;
	height: 3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	background: linear-gradient(55deg, #1a06b9, #0f0080);
	color: #fff;
	font-weight: 500;
	position: absolute;
	right: 0.5rem;
	bottom: 0.5rem;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.todo__item {
	background-color: #fff;
	margin: 0.3rem;
	border-radius: 0.2rem;
	padding: 0.4rem 0.4rem 0.4rem 1rem;
	text-align: left;
	font-size: 1rem;
	-webkit-animation: slide 0.4s ease forwards;
	        animation: slide 0.4s ease forwards;
	transition: 0.3s ease;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.todo__item.todo__done {
	border-left: 0.4rem solid #00bf00;	
}

.todo__item.todo__done .todo__main {
	text-decoration: line-through;
	color: #bbb;
}

.todo__item .delete__btn {
	background-color: #ff6060;
	color: #fff;
	cursor: pointer;
	padding: 0.3rem 0.7rem;
	border: none;
	outline: none;
	border-radius: 0.2rem;
}

@-webkit-keyframes slide {
	from {
		-webkit-transform: translateY(10px);
		        transform: translateY(10px);
	}
	to {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes slide {
	from {
		-webkit-transform: translateY(10px);
		        transform: translateY(10px);
	}
	to {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}
